.container-fluid {
  display: flex;
  flex-direction: column;
  height: 100vh;
  /*       */
}

.user {
  border-bottom: 1px solid #716d6d;
  padding: 10px;
  width: 95%;
}

.user_data {
  background-color: #f8f9fa !important;
  border-top: 5px solid #716d6d;
  max-height: calc(100vh);
}

.user_chat_data {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  /*        */
  overflow: hidden;
  /*      */
}

.channel_list {
  flex-grow: 0;
  /*         */
}

.chat_section {
  padding-top: 15px;
  flex-grow: 1;
  /*         */
  overflow-y: auto;
  /*      */
  padding-bottom: 120px;
  /*         */
}

.profile_name {
  background-color: #f8f9fa !important;
  width: 100%;
  padding: 10px;
  border-bottom: 5px solid #716d6d;
}

.incoming_msg_img {
  display: inline-block;
  width: 6%;
}

.received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 92%;
}

.received_withd_msg p {
  background: #ebebeb none repeat scroll 0 0;
  border-radius: 6px;
  color: #646464;
  font-size: 14px;
  margin: 0;
  padding: 5px 10px 5px 12px;
  width: 100%;
}

.time_date {
  color: #747474;
  display: block;
  font-size: 10px;
  margin: 8px 0 0;
}

.received_withd_msg {
  width: 57%;
}

.mesgs {
  float: left;
  padding: 30px 15px 0 25px;
  width: 60%;
}

.sent_msg p {
  background: #050f8f none repeat scroll 0 0;
  border-radius: 6px;
  font-size: 14px;
  margin: 0;
  color: #fff;
  padding: 5px 10px 5px 12px;
  width: 100%;
}

.outgoing_msg {
  overflow: hidden;
  margin: 26px 0 26px;
}

.sent_msg {
  float: right;
  width: 60%;
}

.input_msg_write input {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  color: #4c4c4c;
  font-size: 15px;
  min-height: 48px;
  width: 100%;
  padding: 10px;
}

.write_msg {
  flex-grow: 1;
  /*        */
  margin: 10px;
  /*     */
}

.type_msg {
  border-top: 1px solid #c4c4c4;
  position: fixed;
  bottom: 0;
  width: calc(100% - 30px);
  /*      */
  background-color: white;
  /*     */
}

.msg_send_btn {
  cursor: pointer;
  font-size: 17px;
  height: 33px;
  position: absolute;
  right: 0;
  top: 50%;
  /*   .input_msg_write      top  50%  */
  transform: translateY(-50%);
  width: 33px;
}

.sent-message {
  float: right;
  /*    */
  margin-left: 10px;
  /*      */
}

.received-message {
  margin-right: 10px;
  vertical-align: top;
}

.chat-nav {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
